.card {
  display: grid;
  grid-template-columns: repeat(4, 23%);
  gap: 25px;
}
.card__components {
  border: 1px solid #cce7d0;
  padding: 10px;
  min-width: 250px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
  transition: 0.2s ease;
}
.card__components:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
}
.card__components .subcomponent {
  display: grid;
  padding: 10px 0;
}
.card__components .subcomponent p {
  margin-top: 2px;
  margin-bottom: 2px;
}
.card__components .subcomponent a {
  justify-self: end;
  padding-right: 5px;
}
.card img {
  width: 100%;
  border-radius: 15px;
}

.addidas {
  color: var(--addidas-text);
  font-size: 12px;
}

.sub {
  font-size: 14px;
  color: var(--nav-text);
  padding-top: 7px;
}

.stars i {
  padding-top: 7px;
  font-size: 12px;
  color: var(--star-color);
}

.money {
  padding-top: 7px;
  font-size: 15px;
  font-weight: 700;
  color: var(--nav-hover);
}

.cart1 {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-weight: 600;
  line-height: 40px;
  color: var(--cart-color);
  background-color: var(--cart-color);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--nav-bar);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
  z-index: 1000;
  position: sticky;
  top: 0;
  left: 0;
}
.header__logo {
  background-color: red;
}
.header__navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.header__navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
.header__navbar li a {
  font-size: 16px;
  font-weight: 600;
  color: var(--nav-text);
  transition: 0.3s ease;
}
.header__navbar li a:hover {
  color: var(--nav-hover);
}

nav li a:hover,
nav li a.active {
  color: var(--nav-hover);
}

nav li a.active::after,
nav li a:hover::after {
  content: "";
  width: 30%;
  height: 2px;
  background: var(--nav-hover);
  position: absolute;
  bottom: -4px;
  left: 20px;
}

main {
  top: 0;
  position: relative;
}

.hero {
  background-image: url("/img/hero4.png");
  background-size: cover;
  height: 90vh;
  width: 100%;
  padding: 25px 80px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.hero__trade {
  font-size: 20px;
  color: var(--hero-text);
  margin-bottom: 0;
  padding-bottom: 15px;
}
.hero__super {
  font-size: 46px;
  line-height: 54px;
  color: var(--hero-text);
  margin-bottom: 0;
}
.hero__products {
  font-size: 50px;
  line-height: 64px;
  color: var(--nav-hover);
  margin-bottom: 0;
}
.hero button {
  background-image: url(/img/button.png);
  background-color: transparent;
  background-size: cover;
  color: var(--nav-hover);
  border: 0;
  padding: 14px 80px 14px 65px;
  background-repeat: none;
  cursor: pointer;
  font-size: 15px;
  font-weight: 700;
}
.hero button:hover {
  color: rgba(255, 15, 16, 0.6);
}

.feature {
  margin-top: 30px;
  padding-top: 25px;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, auto);
  gap: 20px;
}
.feature__card {
  border: 0.5px solid var(--feature-border);
  border-radius: 4px;
  text-align: center;
  padding: 25px 15px;
  box-shadow: 20px 20px 35px rgba(0, 0, 0, 0.06);
  /*img {
     width: 100%;
     margin-bottom: 10px;
  }*/
}
.feature__card .button1 {
  display: inline-block;
  margin-top: 10px;
  padding: 10px 8px;
  cursor: pointer;
  color: var(--nav-hover);
  font-weight: 700;
  border: none;
  border-radius: 5px;
  background-color: var(--button1-bg);
}
