.feature__card:hover {
  box-shadow: 10px 10px 50px rgba(0, 0, 0, 0.1);
}

.para {
  margin-top: 30px;
  text-align: center;
  padding: 20px;
}
.para h2 {
  font-size: 46px;
  line-height: 54px;
  font-weight: 500;
  margin-bottom: 0;
}
.para p {
  margin-top: 10px;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-spartan);
  margin: 0;
  padding: 0;
}

h1, h2, h4 {
  color: var(--hero-text);
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

